@use 'abstracts/mixins' as *;

.jumbotron {
  padding-top: 165px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--white);
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    span {
      color: var(--green-light);
    }

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 22px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--white);
    font-weight: 300;
    margin-bottom: 52px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      margin-bottom: 140px;
    }

    span {
      color: var(--gold);
    }
  }

  &__description {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 55px;
    padding-right: 179px;

    @include md {
      padding-right: 9px;
      padding-bottom: 67px;
    }

    p {
      position: relative;
      max-width: 640px;
      width: 70%;
      color: var(--white);
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 1.2px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.8px;
        width: 100%;
        padding-left: 63px;
        padding-right: 5px;
        max-width: 100%;
      }

      &::before {
        content: '';
        display: block;
        background-color: var(--gold);
        width: 2px;
        margin-top: 2px;
        height: 770px;
        position: absolute;
        top: 0;
        left: -60px;

        @include md {
          height: 616px;
          left: 39px;
        }
      }
    }
  }

  &__parallax {
    background: url('./../assets/esg-parallax-bg.webp');
    min-height: 400px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include md {
      min-height: 340px;
      background-attachment: scroll !important;
      background-position: center !important;
    }
  }
}

.pricing-container {
  background-color: var(--white);
  background-image: url('./../assets/pricing-bg.webp');
  background-position: right -150px top 159px;
  background-size: 876px;
  background-repeat: no-repeat;
  padding-bottom: 50px;

  @include md {
    background-size: 517px;
    background-position: right -297px top 240px;
  }
}

