.intro {
  padding-top: 90px;
  padding-bottom: 40px;

  @include md {
    padding-top: 44px;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }

  &__text {
    flex-shrink: 0;
    margin-right: 100px;
    max-width: 720px;
    padding-right: 12px;
    padding-left: 3px;
    width: 70%;
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 28px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      width: 100%;
      padding-left: 63px;
      margin-right: 0;
      padding-right: 5px;
      margin-bottom: 22px;
    }
  }

  &__title {
    flex-shrink: 0;
    margin-right: 100px;
    max-width: 720px;
    padding-right: 12px;
    padding-left: 3px;
    width: 70%;
    color: var(--mid-green);
    margin-bottom: 19px;
    text-transform: uppercase;
    font-size: 22px;
    letter-spacing: 2.2px;

    @include md {
      width: 100%;
      padding-left: 63px;
      margin-right: 0;
      padding-right: 5px;
      font-size: 16px;
      line-height: 21px;
      letter-spacing: 1.6px;
      margin-bottom: 13px;
    }
  }
}

.pricing__title {
  text-transform: uppercase;
  font-size: 28px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  display: flex;
  align-items: center;
  padding-left: 37px;

  @include md {
    font-size: 16px;
    letter-spacing: 1.6px;
    padding-left: 0;
  }

  &::before {
    content: '';
    display: block;
    background: url('./../assets/title-logo.svg');
    background-size: cover;
    margin-right: 16px;
    position: relative;
    top: 5px;
    height: 72px;
    width: 72px;

    @include md {
      content: '';
      width: 41px;
      margin-right: 9px;
      height: 41px;
      top: -1px;
    }
  }
}

.section-container {
  padding-left: 126px;
  max-width: 1066px;

  @include md {
    padding-left: 0;
  }
}

.pricing {
  &__item {
    background: var(--light-grey);
    padding: 24px 80px 22px 68px;
    margin-bottom: 3px;
    max-width: 878px;

    @include md {
      padding: 37px 20px 37px 70px;
    }

    &__title {
      color: var(--mid-green);
      font-size: 22px;
      letter-spacing: 2.2px;
      text-transform: uppercase;
      padding-right: 10px;

      @include md {
        font-size: 16px;
        line-height: 21px;
        letter-spacing: 1.6px;
      }
    }

    &__content {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.4s ease-in-out;
      &.active {
        max-height: 1000px;
      }
    }

    &__button {
      border: 0;
      background: transparent;
      padding: 0;
      position: absolute;
      left: -38px;
      transform: rotate(270deg);
      cursor: pointer;
    }

    &__price {
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      font-weight: 300;
      font-size: 26px;
      letter-spacing: 0.65px;
      white-space: nowrap;
    }

    &__description {
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      font-weight: 300;
      font-size: 20px;
      letter-spacing: 0.5px;
      margin: 19px 0 17px 0;

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.4px;
      }
    }

    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      flex-wrap: nowrap;
      position: relative;

      @media (width <= 500px) {
        flex-wrap: wrap;

        .pricing__item__price {
          margin-top: 15px;
          width: 100%;
        }
      }

      &--expandable {
        cursor: pointer;
      }

      &.active {
        .pricing__item__button {
          transform: rotate(0);
        }
      }
    }

    &__list {
      padding-left: 33px;

      @include md {
        padding-left: 0;
        margin-bottom: 0;
      }

      &__item {
        font-family: var(--unnamed-font-family-garamond-premier-pro);
        font-weight: 300;
        font-size: 20px;
        line-height: 28px;
        letter-spacing: 0.5px;
        color: var(--dark-grey);
        display: flex;

        @include md {
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0.4px;
        }

        &::before {
          content: 'â€¢';
          color: var(--amber);
          font-size: 20px;
          padding-right: 20px;
        }
      }
    }
  }
}

.pricing-section {
  margin-bottom: 48px;

  &__title {
    text-transform: uppercase;
    font-size: 28px;
    letter-spacing: 2.8px;
    color: var(--mid-green);
    display: flex;
    align-items: center;
    padding-left: 37px;

    @include md {
      font-size: 16px;
      letter-spacing: 1.6px;
      padding-left: 0;
      margin-bottom: 30px;
    }

    &::before {
      content: '';
      display: block;
      background: url('./../assets/title-logo.svg');
      background-size: cover;
      margin-right: 16px;
      position: relative;
      top: 5px;
      height: 72px;
      width: 72px;
      flex-shrink: 0;

      @include md {
        content: '';
        width: 41px;
        margin-right: 9px;
        height: 41px;
        top: -1px;
      }
    }
  }

  &__description {
    color: var(--dark-grey);
    text-align: left;
    font-weight: 300;
    font-size: 20px;
    line-height: 28px;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    letter-spacing: 0.5px;
    margin-bottom: 28px;
    max-width: 820px;
  }
}

.section-container {
  padding-left: 126px;
  max-width: 1066px;

  @include md {
    padding-left: 0;
  }
}

.form {
  background: url('./../assets/pricing-form-bg.webp') no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 92px;
  min-height: 734px;
  text-align: center;

  @include md {
    background-attachment: scroll !important;
    background-position: center !important;
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 55px;
  }

  &__mandatory {
    font: normal normal normal 16px/24px Brandon Grotesque;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;
  }

  @include md {
    padding-top: 69px;
    min-height: 734px;
  }

  &__logo {
    margin-bottom: 50px;

    @include md {
      margin-bottom: 42px;
    }
  }

  &__description {
    text-align: center;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-weight: 300;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    margin-bottom: 70px;
    color: var(--white);

    @include md {
      font-size: 16px;
      margin-bottom: 40px;
      line-height: 22px;
      letter-spacing: 0.8px;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 28px;
    letter-spacing: 2.8px;
    color: var(--light-grey);
    margin-bottom: 46px;

    @include md {
      font-size: 24px;
      letter-spacing: 2.4px;
      margin-bottom: 23px;
      line-height: 26px;
    }
  }

  &__two-columns {
    display: flex;
    gap: 20px;

    @include md {
      gap: 0;
      flex-wrap: wrap;
    }
  }

  form {
    text-align: left;
  }

  &__input {
    border: 0;
    border-bottom: 1px solid var(--white);
    padding: 14px 0;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: var(--white);
    width: 100%;
    background: transparent;
    text-transform: uppercase;
    outline: none;
    margin-bottom: 54px;

    &::placeholder {
      color: var(--white);
    }

    @include md {
      margin-bottom: 34px;
    }
  }

  &__select {
    width: 100%;
    border: 1px solid var(--white);
    background: transparent;
    font-size: 16px;
    letter-spacing: 1.6px;
    padding: 8px 20px;
    color: var(--white);
    margin-bottom: 62px;
    text-transform: uppercase;
    appearance: none;
    outline: none;

    @include md {
      margin-bottom: 34px;
    }

    &-container {
      position: relative;
      width: 70%;
      max-width: 690px;

      @include md {
        width: 100%;
      }

      &::after {
        content: url('./../assets/select-arrow.svg');
        position: absolute;
        top: 10px;
        right: 10px;
      }
    }
  }
  &__button-container {
    max-width: 340px;
    margin-top: 14px;

    @include md {
      max-width: 251px;
    }

    .button-outlined {
      @include md {
        padding-left: 11px;
        font-size: 10px;
        letter-spacing: 1px;
        height: 39px;
      }
    }
  }
}

.dropdown {
  position: relative;
  @include md {
    margin-bottom: 30px;
  }

  &__button {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;
    width: 100%;
    background: transparent;
    border: 1px solid #ffffff;
    height: 36px;
    padding-left: 20px;
    cursor: pointer;
    text-align: left;

    @include md {
      height: 48px;
    }

    &_active {
      background: #162a26;
      &::after {
        transform: translateY(-50%) rotate(-180deg);
      }
    }
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: absolute;
    left: 0;
    top: 36px;
    @include md {
      top: 48px;
    }

    border: 1px solid #fff;
    background: #162a26;
    overflow: hidden;
    width: 100%;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: 200ms ease;
    font-size: 16px;
    letter-spacing: 1.6px;
    color: #ffffff;
    text-transform: uppercase;

    &_visible {
      opacity: 1;
      visibility: visible;
    }

    &-item {
      margin: 0;
      padding: 0;
      padding: 15px;
      cursor: pointer;
      font-size: 15px;
      line-height: 18px;
      color: #fff;
      transition: 200ms ease;
      &_active {
        color: var(--gold);
      }
      &:hover {
        color: var(--gold);
      }
    }
  }
  &__input {
    &_hidden {
      display: none;
    }
  }
}

.form-success-message {
  display: none;
  text-align: left;
  font: normal normal normal 16px/24px Brandon Grotesque;
  letter-spacing: 1.6px;
  color: #ffffff;
  padding-top: 60px;
}
